import CSS from "csstype";
import Rack from "./Rack";
import Selection from "./Selection";
import { useEffect, useState } from "react";
import { Box, Button, IconButton, Modal } from "@mui/material";
import SettingsIcon from "@mui/icons-material/Settings";
import Wordlist from "../Utilities/Wordlist";

const hand_size = 7;

function shuffleRack(rack: (string | null)[]): (string | null)[] {
  let newRack: (string | null)[] = [];
  let picked = 0xff;

  for (let j = 0; j < hand_size; j++) {
    let i = Math.floor(Math.random() * hand_size);
    while (((picked >> i) & 1) === 0) i = Math.floor(Math.random() * hand_size);
    newRack.push(rack[i]);
    picked ^= 1 << i;
  }

  return newRack;
}

function findAllAnswers(word: string, wordlist: string[]): string[] {
  let res = [];
  const w = word.split("").sort().join("");
  for (let word2 of wordlist) {
    const w2 = word2.split("").sort().join("");
    if (w === w2) res.push(word2.toUpperCase());
  }
  return res;
}

const styles: CSS.Properties = {
  justifyContent: "center",
  margin: "auto",
  width: "50%",
};

const modalStyles: CSS.Properties = {
  position: "absolute" as "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  height: "150px",
  width: "400px",
  backgroundColor: "#1f1f1f",
  border: "solid",
  borderWidth: "2px",
  borderColor: "white",
  boxShadow: "24",

  display: "flex",
  alignItems: "center",
  justifyContent: "center",
  textAlign: "center",
  color: "white",
  fontSize: "30px",
};

export default function Game() {
  const [winModalOpen, setWinModalOpen] = useState(false);
  const [invalidModalOpen, setInvalidModalOpen] = useState(false);
  const [useTilesModalOpen, setUseTilesModalOpen] = useState(false);
  const [loseModalOpen, setLoseModalOpen] = useState(false);
  const [answerKeyState, setAnswerKeyState] = useState<string[]>([]);
  const [selectionState, setSelectionState] = useState<(string | null)[]>([
    null,
    null,
    null,
    null,
    null,
    null,
    null,
  ]);
  const [rackState, setRackState] = useState<(string | null)[]>([
    "A",
    "B",
    "C",
    "D",
    "E",
    "F",
    "G",
  ]);

  // remove from selection and add to rack
  function handleClickSelection(idx: number, letter: string) {
    let sRack = [...selectionState];
    sRack[idx] = null;
    setSelectionState(sRack);

    let rack = [...rackState];
    idx = rack.indexOf(null);
    rack[idx] = letter;
    setRackState(rack);
  }

  // remove from rack and add to selection
  function handleClickRack(idx: number, letter: string) {
    let rack = [...rackState];
    rack[idx] = null;
    setRackState(rack);

    let selRack = [...selectionState];
    idx = selRack.indexOf(null);
    selRack[idx] = letter;
    setSelectionState(selRack);
  }

  function clearSelection() {
    let newRack = [...rackState];
    let selRack = [...selectionState.filter((c) => c != null)];
    newRack.forEach((c, i) => {
      if (c == null) newRack[i] = selRack.shift() as string;
    });
    setRackState(newRack);
    setSelectionState([null, null, null, null, null, null, null]);
  }

  function submitSelection() {
    if (selectionState.some((c) => c == null)) {
      setUseTilesModalOpen(true);
      return;
    }
    let s = [...selectionState].join("");
    if (answerKeyState.indexOf(s) !== -1) {
      setWinModalOpen(true);
    } else {
      setInvalidModalOpen(true);
    }
  }

  function getNewWord() {
    let i = Math.floor(Math.random() * Wordlist.length);
    const newWord = Wordlist[i].toUpperCase();
    const answers = findAllAnswers(Wordlist[i], Wordlist);
    console.log("answer keys: " + JSON.stringify(answers));
    setAnswerKeyState(answers);
    setSelectionState([null, null, null, null, null, null, null]);
    let newRack: (string | null)[] = [];
    for (let i = 0; i < newWord.length; i++) {
      let c = newWord.at(i);
      if (c !== undefined) newRack.push(c);
    }
    setRackState(shuffleRack(newRack));
  }

  useEffect(() => getNewWord(), []);

  return (
    <>
      <div
        style={{ display: "static", alignSelf: "flex-end", padding: "20px" }}
      >
        <IconButton
          aria-label="settings"
          size="large"
          color="primary"
          style={{}}
        >
          <SettingsIcon sx={{ fontSize: "50px" }} />
        </IconButton>
      </div>

      <div style={styles}>
        <p
          style={{
            color: "white",
            fontSize: "64px",
            textAlign: "center",
            margin: "0px",
          }}
        >
          GAME NAME
        </p>

        <div className="spacer" style={{ height: "180px" }} />

        <Selection rack={selectionState} onClick={handleClickSelection} />

        <div className="spacer" style={{ height: "100px" }} />

        <Rack rack={rackState} onClick={handleClickRack} />

        <div className="spacer" style={{ height: "80px" }} />

        <div
          style={{
            display: "flex",
            width: "100%",
            justifyContent: "space-around",
          }}
        >
          <Button
            variant="outlined"
            color="error"
            style={{ fontSize: "24px" }}
            onClick={() => setLoseModalOpen(true)}
          >
            Give up (New Word)
          </Button>
          <Modal
            open={loseModalOpen}
            onClose={() => {
              setLoseModalOpen(false);
              getNewWord();
            }}
            aria-labelledby="submit-selection"
          >
            <Box sx={modalStyles}>
              The answer(s): {answerKeyState.join(", ")}
            </Box>
          </Modal>
          <Button
            variant="outlined"
            style={{ fontSize: "24px" }}
            onClick={clearSelection}
          >
            Clear
          </Button>
          <Button
            variant="outlined"
            style={{ fontSize: "24px" }}
            onClick={() => setRackState(shuffleRack(rackState))}
          >
            Shuffle
          </Button>
          <Button
            variant="outlined"
            style={{ fontSize: "24px" }}
            onClick={submitSelection}
          >
            Submit
          </Button>
          <Modal
            open={winModalOpen}
            onClose={() => {
              setWinModalOpen(false);
              getNewWord();
            }}
            aria-labelledby="submit-selection"
          >
            <Box sx={modalStyles}>Yay you found the bingo!</Box>
          </Modal>
          <Modal
            open={invalidModalOpen}
            onClose={() => setInvalidModalOpen(false)}
            aria-labelledby="submit-selection"
          >
            <Box sx={modalStyles}>Invalid word! Try again</Box>
          </Modal>
          <Modal
            open={useTilesModalOpen}
            onClose={() => setUseTilesModalOpen(false)}
            aria-labelledby="submit-selection"
          >
            <Box sx={modalStyles}>Use all tiles before submitting!</Box>
          </Modal>
        </div>
      </div>
    </>
  );
}
